@import "src/assets/mixins";

// облака движ white
.container {
    position: absolute;
    width: 100%;
    top: 950px;
    left: 0;
    margin-top: 20px;
    height: 2000px;
    overflow: hidden;
    z-index: 40;
    pointer-events: none;

    @include xs {
        top: 480px;
        // height: 1726px;
    }

    @media screen and (min-width: 2550px) {
        top: 1200px;
    }

    @media screen and (min-width: 3000px) {
        top: 1350px;
    }

    @media screen and (max-width: 1445px) {
        top: 750px;
    }

    @media screen and (max-width: 1026px) {
        top: 530px;
    }

    @media screen and (max-width: 768px) {
        top: 207px;
    }

    @media screen and (max-width: 575px) {
        top: 334px;
    }
}

@media screen and (max-width: 1024px) {
    .container {
        z-index: 1;
    }
}

.img {
    width: 100%;

}

.wrapper {
    position: relative;
    width: 4000px;
    height: 100%;
    animation: scroll 60s ease-in-out alternate-reverse infinite forwards;
    // animation: scroll 60s linear forwards infinite;
    // padding-bottom: 60px;

    img {
        object-fit: inherit;
    }

    @include xs {
        img {
            width: 100%;
            object-fit: cover;
        }
    }

    @media screen and (max-width: 769px) {
        display: block;
        width: 100%;
    }

    @media screen and (max-width: 768px) {
        animation: none;
    }
}

.clouds_static_background_bottom_white {
    width: 100%;
    height: 100%;
    background-image: url(./../../../public/assets/img/cloud_mobile-2-extra-white.png);
    background-position: 35% 16%;
    background-repeat: no-repeat;
    background-size: 102%;
    z-index: 2;
}

.clouds_static_background_bottom_blue {
    position: absolute;
    top: 69px;
    width: 100%;
    height: 100%;
    background-image: url(./../../../public/assets/img/cloud_mobile2-blue.png);
    background-position: 0% 0%;
    background-repeat: no-repeat;
    background-size: 100%;
    z-index: -1;
}

.cloud_bottom_top {
    width: 100%;
    height: 100%;
    background-image: url(./../../../public/assets/img/cloud_bottom_top.png);
    background-repeat: no-repeat;
    // background-size: 150%;
}

@keyframes scroll {
    from {
        left: -50%;

    }

    to {
        left: 50%;
    }
}