@import "src/assets/mixins";

.content {
    position: relative;
    width: 500px;
    height: 660px;

    @include xs {
        width: 256px;
        height: 345px;
    }
}

:global .swiper-cards {
    max-width: 500px;
    width: 500px;
    height: 660px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(128px);
    border-radius: 25px;

    @include xs {
        width: 256px;
        height: 345px;
    }

    @media screen and (max-width: 768px) {
        backdrop-filter: none;
    }
}

:global .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
}

:global .swiper-button-prev {
    display: block;
    width: 44px;
    height: 44px;
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
    left: -22px;

    @include sm {
        display: none;
    }
}

:global .swiper-button-next {
    display: block;
    width: 44px;
    height: 44px;
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
    right: -22px;

    @include sm {
        display: none;
    }
}

:global .swiper-pagination-bullet {
    height: 6px;
    width: 6px;
    background: linear-gradient(315deg, #DF49C7 0%, #F986BE 100%);
    opacity: 0.2;
    border-radius: 4px;
    cursor: pointer;
}

:global .swiper-pagination-bullet-active {
    opacity: 1;
    height: 9px;
    width: 9px;
}

:global .swiper-pagination {
    display: none;

    @include sm {
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 9px;

        >div {
            margin-right: 9px;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

.next,
.prev {
    width: 44px;
    height: 44px;

    button {
        width: 40px;
        height: 40px;
    }
}

.prev {
    button svg {
        transform: rotate(180deg);
    }
}

.image {
    cursor: pointer;
    object-fit: contain;
    width: 390px;
    height: 562px;
    filter: drop-shadow(0px 0px 7px #FFFFFF) drop-shadow(0px 0px 22px #35C1FD);

    @include xs {
        width: 202px;
        height: 290px;
    }

    z-index: 999;
}