@import 'src/assets/colors';
@import 'src/assets/mixins';


.slider {
    display: flex;
    justify-content: space-between;
    padding-top: 100px;
    padding-bottom: 155px;

    @include md {
        flex-direction: column;
        align-items: center;
        padding-top: 60px;
        padding-bottom: 30px;
    }
}

.description {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 150px;

    @include md {
        margin-top: 50px;
    }
}

.title {
    width: 297px;
    height: 75px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(315.69deg, rgba(243, 116, 181, 0.65) 1.18%, rgba(255, 246, 169, 0.81) 80.97%);
    box-shadow: 0px 0px 19px rgba(0, 0, 0, 0.07);
    border-radius: 20px;
    z-index: 1;

    span {
        background: linear-gradient(315deg, #A60E8D 0%, #FF7ABB 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }

    @include xs {
        width: 191px;
        height: 48px;
    }
}

.content {
    font-size: 24px;
    line-height: 30px;

    @include xs {
        font-size: 15px;
        line-height: 19px;
    }

}

.artist {
    font-size: 18px;
    line-height: 22px;
    margin-top: 5px;

    @include xs {
        font-size: 12px;
        line-height: 15px;
        margin-top: 0;
    }
}

.text {
    margin-top: -35px;
    width: 350px;
    height: 510px;
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(128px);
    border-radius: 25px;
    padding: 63px 34px 14px 43px;

    p {
        font-family: 'Montserrat';
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        color: $coral;
    }

    @include xs {
        margin-top: -22px;
        width: 264px;
        height: 356px;
        padding: 33px 32px 6px 31px;

        p {
            font-size: 14px;
            line-height: 17px;
        }
    }

    @media screen and (max-width: 768px) {
        backdrop-filter: none;
    }
}

.avatar {
    float: left;
    margin-right: 12px;

    @include xs {
        width: 51px;
        height: 52px;
        margin-right: 20px;
    }
}

.sign {
    margin-top: -22px;
    float: right;

    @include xs {
        margin-top: 0;
        width: 76px;
        height: 57px;
    }
}